import React, { useEffect, useState } from 'react';
import {CardGroup, Col, Container, Row } from 'react-bootstrap';
import ProjectCart from './ProjectCart';

const Projects = () => {
    const[projects,setProjects]=useState([]);

    useEffect(()=>{
         fetch('./fakedata.json')
        .then(res=>res.json())
        .then(data=>setProjects(data))
    },[])

    return (
        <Container fluid className="mt-5">
            <Row>
                <Col sm={12}>
                    <h3 className="text-center my-5 project-header">Recent Projects</h3>
                </Col>
            </Row>
            <Row>
            <CardGroup>
                {
                    projects.map(project=><ProjectCart project={project} key={project.id}></ProjectCart>)
                }
           </CardGroup>
        </Row>
    </Container>

    );
};

export default Projects;