import React, { useEffect, useState } from 'react';
import { CardGroup,Card,Container,Row,Col } from 'react-bootstrap';

const Myskills = () => {
    const[skills,setSkills]=useState([]);
    useEffect(()=>{
        fetch('skills.json')
        .then(res=>res.json())
        .then(data=>setSkills(data));
        
    },[]);
    return (
        <Container className="mt-5">
        <Row>
            <Col sm={12}>
                <h3 className="text-center my-5 project-header">My Skills </h3>
            </Col>
        </Row>
       
            
        <CardGroup>
         
          <Row>

          {
                skills.map(skill=>
                    <Col md={4}>
                   
                    <Card className="mx-1 my-2">

                    <Card.Img variant="top" src={skill.img} height='300px' />
                    <Card.Body>
                      <Card.Title>{skill.name}</Card.Title>
                      <Card.Text>
                        {skill.desc}
                      </Card.Text>
                    </Card.Body>
                  
                  </Card>
                  </Col>

                    )
            }
              
          </Row>
           

       </CardGroup>
    
 
    </Container>
    );
};

export default Myskills;